<template>
  <div v-loading="loading"></div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      options: {},
      loading: true,
    };
  },
  activated() {
    setTimeout(() => {
      this.loading = false;
    }, 500);
  },
  deactivated() {
    this.loading = true;
  },
};
</script>

